import React from "react";

import Todoitem from "./todoitem";
const Todocontainer = (props) => {
  return (
    <>
      {props.todo.map((todo) => {
        return (<>
        
          <Todoitem
            key={todo.sno}
            title={todo.title}
            desc={todo.desc}
            todo={todo}
            onDelete={()=>{props.onDelete(todo)}}
            ></Todoitem>
        </>
        );
      })}
    </>
  );
};

export default Todocontainer;
