import React, { useEffect, useState } from "react";
import Todocontainer from "./todocontainer";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import "./form.css";


const Form=()=>{


  let inittodo;
  if(localStorage.getItem("todos")===null)
  inittodo=[];
  else
  inittodo=JSON.parse(localStorage.getItem("todos"));
  
  
  const [todolist, settodo] = useState(inittodo);
  
  useEffect(()=>{
    localStorage.setItem("todos",JSON.stringify(todolist))
  },[todolist])
     
  const ondel=(item)=>{
    settodo(todolist.filter(
  (e)=>{ 
    if(e!==item)
    return e;
  }
    ));
    if(todolist.length===0)
    localStorage("todos","null");
    else
    localStorage.setItem("todos",JSON.stringify(todolist));
  }
  
  
  
    const [title, settile] = useState("");
    const [desc, setdesc] = useState("");
  
    const changevartitle = (e) => {
      settile(e.target.value);
    };
  
    const changevardesc = (e) => {
      setdesc(e.target.value);
    };
  
    const addtask = (event) => {
      event.preventDefault();
  
      if (title && desc) {
        console.log(desc, title);
        let serial;
        if(localStorage.getItem("todos")==='[]')
        {
        serial=1;
        console.log("part1");
      }else{
        console.log("part2");
        serial=todolist[todolist.length-1].sno+1;
      
      }
        const newtask = { "sno": serial, "title": title, "desc": desc };
  
        settodo([...todolist, newtask]);
        console.log(todolist);
        localStorage.setItem("todos",JSON.stringify(todolist));
  
        settile("");
        setdesc("");
      }
  
    };

    return(<>
    <form onSubmit={addtask} className="mx-1">

    <div className="form-group">
  <label for="usr">Title</label>
  <input type="text" className="form-control"  placeholder="Title" value={title} onChange={changevartitle} id="usr"/>
</div>
<div className="form-group">
  <label for="pwd">Description</label>
  <input type="text" placeholder="Description" value={desc} onChange={changevardesc} className="form-control" id="pwd"/>
</div>

        <button type="submit" className="btn btn-primary mt-2 " >Add Task</button>
      </form>
      <section className="mx-1 box" >
      <h3>Tasks to do :</h3>
      {todolist.length===0? "None":
      <Todocontainer todo={todolist} onDelete={ondel}/>
    }
      </section>

    </>)
}


export default Form;